import {
  WS_CONNECTION_START,
  WS_CONNECTION_SUCCESS,
  WS_GET_MESSAGE,
  WS_SEND_MESSAGE,
  WS_CONNECTION_CLOSED,
  WS_CONNECTION_ERROR,
  WS_GET_MESSAGE_PROFILE,
} from "../../actions/soketAction/soketAction";

const initialState = {
  wsConnected: false,
  messagesAllOrders: [],
  messagesUserOrders:[],
  error: undefined,
};

export const wsReducer = (state = initialState, action) => {
  switch (action.type) {
    //      WS_CONNECTION_SUCCESS
    //   wsConnected   true
    case WS_CONNECTION_SUCCESS:
      return {
        ...state,
        error: undefined,
        wsConnected: true,
      };

    //      WS_CONNECTION_ERROR
    //   wsConnected   false     action.payload
    case WS_CONNECTION_ERROR:
      return {
        ...state,
        error: action.payload,
        wsConnected: false,
      };

    //      WS_CONNECTION_CLOSED,   
    //   wsConnected   false
    case WS_CONNECTION_CLOSED:
      return {
        ...state,
        error: undefined,
        wsConnected: false,
      };

    //      WS_GET_MESSAGE
    //  ,     
    //  messages  ,    
    case WS_GET_MESSAGE:
      return {
        ...state,
        error: undefined,
        messagesAllOrders: [ JSON.parse(action.payload)],
      };
    case WS_GET_MESSAGE_PROFILE:
      return {
        ...state,
        error: undefined,
        messagesUserOrders: [ JSON.parse(action.payload)],
      };
    default:
      return state;
  }
};
